.clearfix::after {
    content: "";
    clear: both;
    display: table;
}
.f-left {
    float: left;
}
.f-right {
    float: right;
}
.text-upper {
    text-transform: uppercase;
}
.no-style-list {
    padding: 0;
    margin: 0;
    list-style: none;
}
// display
.flex {
    display: flex;
    flex-wrap: wrap;
}
.justify-space-between {
    justify-content: space-between;
}
.block {
    display: block;
}
// alignment
.text-center {
    text-align: center;
}
.text-left {
    text-align: left;
}
.text-right {
    text-align: right;
}
.vertical-center > * {
    vertical-align: middle;
}
.object-fit-cover {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0,0,0,0);
    white-space: nowrap;
    border: 0;
}