.notification {
  position: fixed;
  top: 16px;
  right: 16px;
  left: 16px;
  color: var(--c-black);
  font-size: 1.125rem;
  font-weight: 500;
  padding: 16px 40px;
  border-radius: var(--size-2);
  text-align: center;
  z-index: 99;
  transform: translateY(-150%);
  visibility: hidden;
  opacity: 0;
  transition: transform 250ms;
  background: var(--c-success-light);
}
.notification.success {
  background: var(--c-success-light);
}
.notification.error {
  background: var(--c-error-light);
}
.notification.visible {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.notification-close {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.notification-close .close-icon {
  position: absolute;
  top: 16px;
  right: 16px;
  color: var(--c-black);
}

.success .notification.success {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
.success .notification.success.hidden {
  transform: translateY(-150%);
  visibility: hidden;
  opacity: 0;
}

.error .notification.error {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
.error .notification.error.hidden {
  transform: translateY(-150%);
  visibility: hidden;
  opacity: 0;
}

