html {
	box-sizing: border-box;
	font-size: 100%;
	height: 100%;
	width: 100%;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	font-family: var(--font-family);
	line-height: 1.5;
	color: var(--c-text-main);
	background-color: var(--c-body-bg);
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: grayscale;
	height: 100%;
	width: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
	color: var(--c-text-main);
	font-weight: 600;
	line-height: 1.2;
	margin-bottom: 1rem;
}
h1,
.h1 {
	font-size: 3rem; //48px
}
h2,
.h2 {
	font-size: 2.5rem; //40px
}
h3,
.h3 {
	font-size: 2rem; //32px
}
h4,
.h4 {
	font-size: 1.5rem; //24px
}
h5,
.h5 {
	font-size: 1.25rem; //20px
}
h6,
.h6 {
	font-size: 1rem; //16px
}

.heading-large {
	font-size: 4rem; //64px
}
// small screen
@media (max-width: 575px) {
	h1,
	.h1 {
		font-size: 2rem; //48
	}
	h2,
	.h2 {
		font-size: 1.75rem; //48
	}
	h3,
	.h3 {
		font-size: 1.5rem; //24
	}
	h4,
	.h4 {
		font-size: 1.25rem; //18
	}
	h5,
	.h5 {
		font-size: 1.125rem; //16
	}
	h6,
	.h6 {
		font-size: 1rem; //14
	}
	.heading-large {
		font-size: 2.5rem; //64px
	}
}
a {
	text-decoration: none;
	color: var(--c-primary-main);
	transition: all 250ms;
	&:hover {
		text-decoration: none;
	}
}
img {
	max-width: 100%;
	height: auto;
	display: block;
}

svg {
	fill: currentColor;
	width: 24px;
	height: 24px;
}
.btn {
	&:not(:disabled):not(.diabled) {
		cursor: pointer;
	}
	display: inline-block;
	white-space: nowrap;
	text-align: center;
	text-transform: capitalize;
	font-size: 1rem;
	font-weight: 500;
	vertical-align: middle;
	user-select: none;
	padding: 0px 1rem;
	border-radius: var(--size-1);
	height: 48px;
	line-height: 48px;
	border: none;
	background-color: var(--c-primary-main);
	color: var(--c-white);
	transition: all 250ms;
	outline: 0;
	align-items: center;
	&:hover,
	&:focus {
		background-color: var(--c-primary-dark);
		text-decoration: none;
	}
	svg {
		width: 24px;
		height: 24px;
		margin-right: 8px;
	}
}
.btn-alternate {
	background-color: var(--c-white);
	color: var(--c-black);
	&:hover,
	&:focus,
	&:active {
		background-color: var(--c-white);
		color: var(--c-primary-main);
	}
}
.btn-flat {
	background-color: var(--c-body-bg);
	color: var(--c-text-main);
	&:hover,
	&:focus,
	&:active {
		background-color: var(--c-body-bg);
		color: var(--c-primary-main);
	}
	&.loading {
		&::after {
			content: none !important;
		}
	}
}
.btn-sm {
	height: 40px;
	line-height: 40px;
}
.btn-block {
	width: 100%;
}
input[type="text"],
input[type="email"],
input[type="url"],
textarea {
	background: var(--c-body-bg);
	border: 1px solid var(--c-border);
	box-shadow: none;
	height: 48px;
	width: 100%;
	padding: 0px 1rem;
	margin-bottom: 16px;
	color: var(--c-text-main);
	border-radius: var(--size-1);
	&:focus {
		border: 1px solid var(--c-primary-main);
		color: var(--c-text-main);
		outline: none;
		box-shadow: none;
	}
	&::placeholder {
		color: var(--c-text-light);
	}
}
textarea {
	height: auto;
	padding: 1rem;
}
.input-field {
	margin-bottom: 16px;
	&:last-child {
		margin-bottom: 0;
	}
}
.form-field {
	width: 100%;
}
@media (min-width: 768px) {
	.field-group-inline {
		display: flex;
		flex-wrap: wrap;
		.form-field {
			width: auto;
			margin-bottom: 0;
			&:not(:last-child) {
				margin-right: 12px;
			}
		}
		.input-field {
			flex: 1 0 200px;
			width: auto;
		}
	}
}

blockquote {
	font-size: 1.5rem;
	line-height: 1.5;
	margin: 2.5rem 0;
	padding: 2rem;
	position: relative;
	font-weight: 700;
	color: var(--c-white);
	overflow: hidden;
	&::before {
		content: "";
		background-image: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat;
		background-size: 180px;
		opacity: 0.06;
		width: 180px;
		height: 180px;
		position: absolute;
		left: -20px;
		top: -40px;
		z-index: -1;
	}
	&::after {
		content: "";
		background-color: var(--c-primary-main);
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		position: absolute;
		border-radius: var(--size-2);
		z-index: -2;
	}
}
figure {
	margin-left: 0;
	margin-right: 0;
}
figcaption {
	font-size: 1rem;
	text-align: center;
	margin-top: 8px;
}
.table-responsive {
	width: 100%;
	display: block;
	overflow-x: auto;
	margin-bottom: 2rem;
	table {
		margin-bottom: 0;
	}
}
hr {
	border: none;
	border-bottom: 1px solid var(--c-border);
	margin: 4rem 0;
}
table {
	border-collapse: collapse;
	width: 100%;
	max-width: 100%;
	overflow: auto;
	font-size: 1rem;
}
table,
th,
td {
	border: 1px solid var(--c-border);
}
th {
	color: var(--c-text-main);
}
th,
td {
	padding: 8px 16px;
}
dd,
dt {
	line-height: 24px;
}
dl,
ol,
ul {
	padding-left: 1.3rem;
}
pre {
	overflow: auto;
	background-color: var(--c-gray-lighter);
	border-radius: var(--size-2);
	border: 1px solid var(--c-border-light);
	padding: 1rem;
	margin: 2.5rem 0;
	overflow: auto;
	font-size: 1rem;
	line-height: 1.5;
}
code {
	font-family: Consolas, Menlo, Monaco, "Andale Mono WT", "Andale Mono",
		"Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono",
		"Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L",
		"Courier New", Courier, monospace;
}
:not(pre) > code {
	font-size: 0.875em;
	background-color: var(--c-gray-light);
	padding: 2px 6px;
	border: 1px solid var(--c-border);
	border-radius: var(--size-1);
}
.label {
	display: inline-flex;
	white-space: nowrap;
	font-size: 0.875rem;
	font-weight: 600;
	height: 28px;
	line-height: 28px;
	padding: 0 16px;
	border-radius: 14px;
}
.label-success {
	background: var(--c-success-light);
	color: var(--c-black);
}
.label-success-solid {
	background: var(--c-success-dark);
	color: var(--c-white);
}
.site-wrap {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	overflow: hidden;
}
[data-nav="sticky"] {
	.site-wrap {
		margin-top: 88px;
		padding-top: 40px;
	}
}
.main {
	flex-grow: 1;
}
@keyframes loading {
	to {
		transform: rotate(360deg);
	}
}
%loading_spinner {
	position: relative;
	pointer-events: none;
	&:after {
		content: "";
		box-sizing: border-box;
		position: absolute;
		top: 50%;
		left: calc(50% - 10px);
		width: 20px;
		height: 20px;
		margin-top: -10px;
		border-radius: 50%;
		border: 3px solid var(--c-white);
		border-top-color: transparent;
		animation: loading 0.8s linear infinite;
	}
}
.loading {
	.btn,
	&.btn,
	button,
	&.button,
	input[type="submit"],
	&input[type="submit"] {
		@extend %loading_spinner;
		span {
			visibility: hidden;
		}
	}
}
%success_icon {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 50%;
		display: inline-block;
		height: 10px;
		width: 20px;
		border: 3px solid var(--c-white);
		border-top-width: 0;
		border-right-width: 0;
		transform: translate(-50%, -75%) rotate(-45deg);
	}
}
.success {
	.btn,
	&.btn,
	button,
	&.button,
	input[type="submit"],
	&input[type="submit"] {
		@extend %success_icon;
		span {
			visibility: hidden;
		}
	}
}
/* medium zoom fix */
.medium-zoom-overlay {
	z-index: 4000000;
}
.medium-zoom-image--opened {
	z-index: 4000001;
}
